import React from 'react';
import { IconButton, Typography, AppBar, CssBaseline, Toolbar, Container, Box, Button, ToggleButtonGroup, ToggleButton, Dialog, DialogContent, DialogContentText, DialogTitle } from '@mui/material';
import WbSunnyIcon from '@mui/icons-material/WbSunny';
import Brightness4Icon from '@mui/icons-material/Brightness4';
import Brightness7Icon from '@mui/icons-material/Brightness7';
import { ThemeProvider, createTheme, } from '@mui/material/styles';
import CircleIcon from '@mui/icons-material/Circle';
import {red, teal, purple, lime, blue} from'@mui/material/colors';

const App = () => {

    const [theme, setTheme] = React.useState(createTheme());
    const [alignment, setAlignment] = React.useState(() => {
        console.log("run function");
    });

    const [open, setOpen] = React.useState(() => {
        console.log("run function 1");
        return false;
    });
    const handleChange = (event, newAlignment) => {
        setAlignment(newAlignment);
    };

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
        setAlignment();
    };

    const changeTheme = (newTheme) => {
        setTheme((prevTheme) => {
            if (newTheme === 'red' && prevTheme.palette.primary.main !== red[500]) {
                return createTheme(theme,
                    {
                        palette: {
                            primary: {
                                main: red[500],
                              },
                        },
                    }); 
            } else if (newTheme === 'dark' && prevTheme.palette.mode !== 'dark') {
                return createTheme(
                    {
                        palette: {
                            mode: "dark",
                        },
                    });
            } else if (newTheme === 'teal' && prevTheme.palette.primary.main !== teal[300]) {
                return createTheme(theme,
                    {
                        palette: {
                            primary: {
                                main: teal[300],
                              },
                        },
                    }); 
            } else if (newTheme === 'purple' && prevTheme.palette.primary.main !== purple[500]) {
                return createTheme(theme,
                    {
                        palette: {
                            primary: {
                                main: purple[500],
                              },
                        },
                    }); 
            }
            else if (newTheme === 'lime' && prevTheme.palette.primary.main !== lime[500]) {
                return createTheme(theme,
                    {
                        palette: {
                            primary: {
                                main: lime[500],
                              },
                        },
                    }); 
            }
            return createTheme();
        });
    };


    return (
        <>
        <ThemeProvider theme={theme}>
            <Box component="span" m={1} display="flex" justifyContent="left" alignItems="center">
                <Typography variant='p15'>
                        Try different themes!
                </Typography>
                <IconButton sx={{ ml: 1 }} onClick={()=>changeTheme('dark')} color="inherit">
                    {theme.palette.mode === 'dark' ? <Brightness7Icon /> : <Brightness4Icon />}
                </IconButton>
                <IconButton onClick={()=>changeTheme('red')}>
                    {theme.palette.primary.main === red[500] ?
                    <CircleIcon sx={{color: blue[500]}}  /> : 
                    <CircleIcon sx={{color: red[500]}}/>
                    }
                </IconButton>
                <IconButton onClick={()=>changeTheme('teal')}>
                    {theme.palette.primary.main === teal[300] ?
                    <CircleIcon sx={{color: blue[500]}} /> : 
                    <CircleIcon sx={{color: teal[300]}} />
                    }
                </IconButton>
                <IconButton onClick={()=>changeTheme('purple')}>
                    {theme.palette.primary.main === purple[500] ?
                    <CircleIcon sx={{color: blue[500]}} /> : 
                    <CircleIcon sx={{color: purple[500]}} />
                    }
                </IconButton>
                <IconButton onClick={()=>changeTheme('lime')}>
                    {theme.palette.primary.main === lime[500] ?
                    <CircleIcon sx={{color: blue[500]}} /> : 
                    <CircleIcon sx={{color: lime[500]}} />
                    }
                </IconButton>
            </Box>
            <CssBaseline />
            <AppBar position='relative'>
                <Toolbar>
                    <WbSunnyIcon style={{ marginRight: '20px' }} />
                    <Typography variant='h6' >
                        Sunshine Clinic
                    </Typography>
                </Toolbar>
            </AppBar>
            <Box sx={{ paddingTop: 2 }} />
            <main>
                <div>
                    <Container maxWidth='md'>
                        <Typography variant='h6' align='left' color='textPrimary' gutterBottom>
                            What is your biological sex?
                        </Typography>
                    </Container>
                    <Box sx={{ paddingTop: 2 }} />
                    <Container maxWidth='md'>
                        <Box component="span" m={1} display="flex" justifyContent="center" alignItems="center">
                            <ToggleButtonGroup
                                fullWidth={true}
                                orientation="vertical"
                                color="primary"
                                value={alignment}
                                exclusive
                                onChange={handleChange}
                            >
                                <ToggleButton  value="0">Male</ToggleButton>
                                <ToggleButton  value="1">Female</ToggleButton>
                                <ToggleButton value="2">Other Options</ToggleButton>

                            </ToggleButtonGroup>
                        </Box>
                    </Container>
                    <Box sx={{ paddingTop: 2 }} />
                    <Container maxWidth='md'>
                        <Box component="span" m={1} display="flex" justifyContent="space-between" alignItems="center">
                            <Button onClick={handleClickOpen} size="large" color="primary" variant="contained" >Back</Button>
                            <Button onClick={handleClickOpen} size="large" color="success" variant="contained" disabled={!alignment}>Continue</Button>
                        </Box>
                    </Container>

                    <Dialog
                        open={open}
                        onClose={handleClose}
                        aria-labelledby="alert-dialog-title"
                        aria-describedby="alert-dialog-description"
                    >
                        <DialogTitle id="alert-dialog-title">
                            {"This is just a demo!"}
                        </DialogTitle>
                        <DialogContent>
                            <DialogContentText id="alert-dialog-description">
                                This is just a demo of what Material UI can accomplish.  😃
                            </DialogContentText>
                        </DialogContent>
                    </Dialog>
                </div>
            </main>
        </ThemeProvider>

        </>
    );
}

export default App;